<template>
    <div>
        <div class="login_header clearfix">
			<div class="container1 clearfix">
				<div class="login-sign">
					<img src="../../assets/img/hospital.png" >
				</div>
				<h2>病理信息管理系统</h2>
			</div>
		</div>
		<div class="login_main clearfix">
			<div class="container1 clearfix">
				<div class="middle-box">
					<aside class="aside  hidden-sm hidden-xs">
						<img src="../../assets/img/login_pic.png">
					</aside>
					<article class="login-box clearfix">
						<div class="login-box-title">
							<span class="span1">用户登录 &nbsp;/</span>
							<span class="span2">LOGIN</span>
						</div>
						<!-- <form action="http://localhost:3002/check" method="POST"> -->
							<div class="login-id clearfix">
								<span>用户名：</span>
								<input type="text" name="username" class="username" v-model="username">
							</div>
							<div class="login-pw clearfix">
								<span>密&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;码：</span>
								<input type="password" name="password" class="password" v-model="password">
							</div>
							<div class="login-section ">
								<el-radio  v-model="radio" label="user" @change="onChange">医护人员</el-radio>
                                <el-radio  v-model="radio" label="manger" @change="onChange">管理员</el-radio> 
							</div>
							<div class="btn-box">
								<button type="submit" class="login-btn" @click="onSubmit">登录</button>
								<button type="button" class="register-btn" @click="$router.push('/register')">注册</button>
							</div>
						<!-- </form> -->
					</article>
				</div>
			</div>
			
			
			
		</div>
    </div>
</template>

<script>
// import Qs from 'qs'
    export default {
		data(){
			return {
				username:'',
				password:'',
				radio:'user'

			}
		},
		methods:{
			onChange(){
				// console.log(this.radio);
			},
			onSubmit(){
				// let _this = this;
				let username =  this.username;
				let password = this.password;
				let job = this.radio;

				if(username == ''){
					alert('用户名不能为空')
				}else if(password == ''){
					alert('密码不能为空')
					
				}else{

					// let data ={params:"3333"}

					// this.$axios.post("/test1",Qs.stringify(data)).then((res)=>{
					// 	console.log(res)

					// })



					this.$axios.get("/login",{params:{
						username:username,
						password:password,
						job:job
					}},{
						headers:{
							'Content-Type': 'application/json'
						}
					}).then((res)=>{
						// 请求数据成功后提示
						// console.log(res.data.id)
						if(res.data.isLogin){
							// sessionStorage.username = this.username;
							sessionStorage.setItem("my_username",this.username);
							sessionStorage.setItem("my_id",res.data.id);
							sessionStorage.setItem("my_purview",this.radio);
							this.$router.push('/dashboard')
						}else{
							alert('登陆失败，请检查输入是否正确')
						}
						
					})
					

				}


			}
		}
    }
</script>

