<template>
    <div id="app">
        <el-container>
            <el-header style="padding=0px;" height="70px" class="elHeader">
                <div class="header">
                    <!-- 折叠按钮 -->
                    <div class="collapse-btn" >
                        <i  class="el-icon-s-fold"></i>
                        <!-- <i v-else class="el-icon-s-unfold"></i> -->
                    </div>
                    <div class="logo">病理信息管理系统</div>
                </div>
            </el-header>    

        </el-container>



        <div class="main_container">
            <h1 class="title">注册</h1>
            
            <div class="container">
                <div class="form-box">
                    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                        <el-form-item label="用户名" prop="checkUser">
                            <el-input type="text" v-model="ruleForm.username" autocomplete="off"></el-input>
                        </el-form-item>

                        <el-form-item label="密码" prop="pass">
                            <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="确认密码" prop="checkPass">
                            <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
                        </el-form-item>


                        <el-form-item label="真实姓名" prop="checkName">
                            <el-input type="text" v-model="ruleForm.realname" autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="证件号" prop="idNumber">
                            <el-input type="text" v-model="ruleForm.idNumber" autocomplete="off"></el-input>
                        </el-form-item>
                        

                        <el-form-item label="性别">
                            <el-radio-group v-model="form.sex">
                                <el-radio label="男"></el-radio>
                                <el-radio label="女"></el-radio>                              
                            </el-radio-group>
                        </el-form-item>

                        <!-- <el-form-item label="城市级联">
                            <el-cascader :options="options" v-model="form.options" @change="handleChange"></el-cascader>
                        </el-form-item>

                        <el-form-item label="具体地址">
                            <el-input type="text" v-model="form.address" autocomplete="off"></el-input>
                        </el-form-item> -->
                        <el-form-item label="联系电话" prop="phone">
                            <el-input type="text" v-model="ruleForm.phone" autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="邮箱" prop="email">
                            <el-input type="text" v-model="ruleForm.email" autocomplete="off"></el-input>
                        </el-form-item>
                        

                        
                        <el-form-item class="bottom">
                            <el-button class="btn" type="primary" @click="onSubmit">表单提交</el-button>
                            <el-button class="btn" @click="$router.push('/login')">返回</el-button>
                        </el-form-item>
                    </el-form>
                </div>
            </div>
        </div>




        
    </div>
</template>

<script>
// import { fetchData } from '../../api/index';
// import topHeader from '../public/Header.vue'
// import areaList from "./erea.js";
import { regionData,CodeToText } from 'element-china-area-data'
export default {
    name: 'baseform',

    data() {
        var validatePass = (rule, value, callback) => {
            if (value == '') {
                callback(new Error('请输入密码'));
            } else {
            if (this.ruleForm.checkPass !== '') {
                this.$refs.ruleForm.validateField('checkPass');
            }
                callback();
            }
        };
        var validatePass2 = (rule, value, callback) => {
            // console.log(value)
            if (value == '') {
                callback(new Error('请再次输入密码'));
            } else if (value !== this.ruleForm.pass) {
                callback(new Error('两次输入密码不一致!'));
            } else {
                callback();
            }
        };
        var validateUsername = (rule, value, callback) => {
            // console.log(this.ruleForm.username);
            if(this.ruleForm.username == ''){
                callback(new Error('请输入用户名'))
                // return;
            }
            for(let i = 0;i<this.userList.length;i++){
                // console.log(this.userList[i].username)
                if(this.ruleForm.username == this.userList[i].username ){
                     callback(new Error('用户名已被占用'));
                }else if(i == this.userList.length){
                    callback()
                }
            }
        };
        var chnName = (rule, value, callback) => {
            if (!this.ruleForm.realname) {
                callback(new Error('请输入中文名'))
            } else {
                const reg = /^[\u4e00-\u9fa5]+$/;
                if (this.ruleForm.realname.length < 100 && reg.test(this.ruleForm.realname)) {
                    callback()
                } else {
                    return callback(new Error('请输入正确的中文名'))
                }
            }
        }



        return {
            // nameList:[],
            username:'',
            ruleForm: {
                username:'',
                pass: '',
                realname:'',
                checkPass: '',
                age: '',
                email:'',
                idNumber:'',
                phone:''
            },
            rules: {
                pass: [
                    {required: true, validator: validatePass, trigger: 'blur' }
                ],
                checkPass: [
                    {required: true, validator: validatePass2, trigger: 'blur' }
                ],
                checkUser: [
                    {required: true, validator: validateUsername, trigger: 'blur' }
                ],
                checkName: [
                    {required: true, validator: chnName, trigger: 'blur' }
                ],
                email: [{
                        required: true,//是否必填
                        message: '请输入邮箱地址',//错误提示信息
                        trigger: 'blur'//检验方式（blur为鼠标点击其他地方，）
                    },
                    {
                        type: 'email',//要检验的类型（number，email，date等）
                        message: '请输入正确的邮箱地址',
                        trigger: ['blur', 'change']//（change为检验的字符变化的时候）
                    }
                ],
                idNumber: [
                    { required: true, message: "请输入身份证号", trigger: "blur" },
                    { pattern:/(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)/, message: "请输入合法身份证号", trigger: "blur"}
                ],
                phone: [
                    { required: true, message: "请输入手机号", trigger: "blur" },
                    // 这个只能验证手机号
                    // { pattern:/^0{0,1}(13[0-9]|15[7-9]|153|156|18[7-9])[0-9]{8}$/, message: "请输入合法手机号", trigger: "blur" }
                    { pattern:/^((0\d{2,3}-\d{7,8})|(1[3584]\d{9}))$/, message: "请输入合法手机号/电话号", trigger: "blur" }
                ],
                
            },
      
            options: regionData,
            form: {
                
                realname:'',
                sex: '男',
                idcard:'',
                address:'',
                tel:'',
                mail:''
            }
        };
    },
    methods: {
        onSubmit() {
            // let address = this.city + this.form.address;
            // console.log(this.form.sex)
            var _this = this
            if(this.ruleForm.username == "" || this.ruleForm.password == "" || this.ruleForm.realname == ""){               
                    this.$message.error('请填写完整');                
            }else{
                this.$axios.get("/waiteradd",{params:{
                    username:this.ruleForm.username,
                    password:this.ruleForm.pass,
                    realname:this.ruleForm.realname,
                    sex:this.form.sex,
                    tel:this.ruleForm.phone,
                    mail:this.ruleForm.email,
                    idcard:this.ruleForm.idNumber,
                }}).then(()=>{
                    _this.$message({
                        message: '注册申请已提交，正在等待管理员审批',
                        type: 'success'
                    });
                    _this.$router.push('/login')
                })
            }
        },
        test(){
            // console.log(this.form.options);
        },
        handleChange (value) {
            // console.log(CodeToText[value[0]])
            this.city = CodeToText[value[0]]+CodeToText[value[1]]+CodeToText[value[2]]
        }
    },
    created(){

        this.$axios.get("/getusername").then((res)=>{
            this.userList = res.data;
            // console.log(res.data);
        })
    }

}
</script>

<style lang="scss" scoped>

.title{
    // margin-bottom: 30px;
    height: 70px;
    line-height: 70px;
}
.bottom{
    text-align: center;
}


.clearfix::after{
    content: "";
    display: block;
    clear: both;
}
.el-container{
  padding:0px;
}

*{
  padding: 0px;
  margin:0px;
}

// ----注册主干-----
.main_container{
    width: 1220px;
    margin:0 auto;
    h1{
        text-align: center;
    }
}
.el-form-item{
    margin-bottom: 18px;
}
.el-radio{
    margin-right: 20px;
}
.btn{
    width:100px;
    height: 40px;
    line-height: 40px;
    text-align: center;
}




// ----注册主干-end----







// ------头部---------
.header {
    position: relative;
    box-sizing: border-box;
    width: 100%;
    height: 70px;
    font-size: 22px;
    color: #fff;
    background-color: #00BB88;
}
.collapse-btn {
    float: left;
    padding: 0 21px;
    cursor: pointer;
    line-height: 70px;
}
.header .logo {
    float: left;
    width: 250px;
    line-height: 70px;
}
.header-right {
    float: right;
    padding-right: 50px;
}
.header-user-con {
    display: flex;
    height: 70px;
    align-items: center;
}
.btn-fullscreen {
    transform: rotate(45deg);
    margin-right: 5px;
    font-size: 24px;
}
.btn-bell,
.btn-fullscreen {
    position: relative;
    width: 30px;
    /* height: 30px; */
    text-align: center;
    border-radius: 15px;
    cursor: pointer;
}
.btn-bell-badge {
    position: absolute;
    right: 0;
    top: 20px;
    width: 8px;
    height: 8px;
    border-radius: 4px;
    background: #f56c6c;
    color: #fff;
}
.btn-bell .el-icon-bell {
    color: #fff;
}
.user-name {
    margin-left: 10px;
}
.user-avator {
    margin-left: 20px;
}
.user-avator img {
    display: block;
    width: 40px;
    height: 40px;
    border-radius: 50%;
}
.el-dropdown-link {
    color: #fff;
    cursor: pointer;
}
.el-dropdown-menu__item {
    text-align: center;
}
// ------头部--end-------
</style>
